import { animated, useSpring } from "@react-spring/web";
import { HTMLAttributes, useState } from "react";
import styled from "styled-components";
import { configLinearFast } from "../DynamicMenu";

interface Props extends HTMLAttributes<HTMLButtonElement> {
  maskText?: string;
}
export function MaskedButton({ maskText, children, ...props }: Props) {
  const [hover, setHover] = useState(false);
  const [opacity] = useSpring(
    () => ({
      from: { opacity: 0 },
      to: { opacity: hover ? 1 : 0 },
      config: configLinearFast,
    }),
    [hover]
  );
  return (
    <MaskedButtonWrapper
      {...props}
      onMouseEnter={(e) => {
        setHover(true);
        props.onMouseEnter?.(e);
      }}
      onMouseLeave={(e) => {
        setHover(false);
        props.onMouseLeave?.(e);
      }}
    >
      {children}
      <MaskedButtonMask style={opacity}>{maskText}</MaskedButtonMask>
    </MaskedButtonWrapper>
  );
}

const MaskedButtonWrapper = styled.button`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  border: 1px solid ${(p) => p.theme.colors.noColor.Grey4};
  border-radius: 3px;
`;

const MaskedButtonMask = styled(animated.div)`
  position: absolute;
  top: 0;
  left: 0;
  color: ${(p) => p.theme.colors.noColor.Grey4};
  background-color: ${(p) => p.theme.colors.Neutral_400};
  cursor: pointer;
  text-align: start;
  border-radius: 3px;
  width: 100%;
  height: 100%;
  display: flex;
  ${(p) => p.theme.flexCSS.center}
  padding: 12px;
  box-sizing: border-box;
`;
